import Times from '../../public/static/icons/times-solid.svg';
import ExclamationTriangle from '../../public/static/icons/exclamation-triangle-solid.svg';
import Excel from '../../public/static/icons/file-excel-solid.svg';
import ExternalLinkAlt from '../../public/static/icons/external-link-alt-solid.svg';
import styles from './icon.module.scss';

export const IconNames = {
  TIMES: 'times',
  EXCLAMATION_TRIANGLE: 'exclamation-triangle',
  EXCEL: 'excel',
  EXT_LINK_ALT: 'ext-link-alt'
};

const iconMap = {
  [IconNames.TIMES]: Times,
  [IconNames.EXCLAMATION_TRIANGLE]: ExclamationTriangle,
  [IconNames.EXCEL]: Excel,
  [IconNames.EXT_LINK_ALT]: ExternalLinkAlt
};

function Icon({ name, baseline=false, className='' }) {
  const SelectedComponent = iconMap[name];
  if (!SelectedComponent) { return null; }

  let classes = className ? className.split(' ') : [];
  classes.push(styles['svg-icon']);
  if (baseline) {
    classes.push(styles['svg-baseline']);
  }

  return (
    <span className={classes.join(' ')}>
      <SelectedComponent></SelectedComponent>
    </span>
  );
}

export default Icon;